import React, {useState} from 'react'
import SearchIcon from '@material-ui/icons/Search';
import Upstox from './Sidebar/Upstox';
import Zerodha from './Sidebar/Zerodha';
import Groww from './Sidebar/Groww';
import './Sidebar.css'


function Sidebar() {

    return (
        <div className="sidebar"> 
 
           <div className="searchBar">
               <SearchIcon className="search__icon"/>
               <input className ="search__input" type="text" placeholder="Search Eg. Zerodha, Grow, Upstox "></input>
            </div> 

            <div className="sidebar__options">
                <hr className="sidebar__rule"/>
                <Upstox />
                <hr className="sidebar__rule"/>
                <Zerodha />
                <hr className="sidebar__rule"/>
                <Groww />
                <hr className="sidebar__rule"/>
            </div>


            <div className="footer">
                <span className="footer__button1">
                        <p>All Companies</p>
                </span>
                <span className="footer__button2">
                        <p>Watchlist</p>
                </span>
            </div>
            
        </div>
    )
}

export default Sidebar
