import React from "react";
import { ResponsiveContainer,PieChart, Pie, Cell, Tooltip, Legend } from 'recharts';
import './Charts.css';
class PieRechartComponent extends React.Component {

    COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#AF19FF'];

    pieData = [
        {
          "name": "RELIANCE POWER",
          "value": 20.14
        },
        {
          "name": "INDIAN POWER CORPORATION",
          "value": 7.91
        },
        {
            "name": "TATA POWER",
            "value": 15.85
        },

        {
            "name": "PO",
            "value": 6.85
        },

        {
            "name": "Others",
            "value": 10.25
        }
    ];

    CustomTooltip = ({ active, payload, label }) => {
        if (active) {
            return (
                <div className="custom-tooltip" style={{ backgroundColor: '#ffff', padding: '5px', border: '1px solid #cccc' }}>
                    <label>{`${payload[0].name} : ${payload[0].value}%`}</label>
                </div>
            );
        }

        return null;
    };

    render() {
        return (
          <div className="pie">

                <PieChart width={500} height={400}>
                <Pie data={this.pieData} color="#000000" dataKey="value" nameKey="name" cx={120} cy={200} innerRadius={40} outerRadius={80} fill="#8884d8" >
                    {
                        this.pieData.map((entry, index) => <Cell key={`cell-${index}`} fill={this.COLORS[index % this.COLORS.length]} />)
                    }
                </Pie>
                <Tooltip content={<this.CustomTooltip />} />
                <Legend  layout="vetical" verticalAlign="middle" align="right"/>
               </PieChart>

          </div>

        )
    };
}

export default PieRechartComponent;