import React, {useState} from 'react'
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';
import BookMarkIcon from "@material-ui/icons/Bookmark";
import './Zerodha.css';
function Zerodha() {
    const [bookmark, setBookmark] = useState(false);
    return (
 
        <div className="list">
           
                <p className="title">ZERODA</p>
                <div className = "title__options">
                    <p className="buy">Buy</p>
                    <p className="sell">Sell</p>
                    
                    {
                        bookmark===true ? <BookMarkIcon className="bookmark__icon" onClick = {()=>setBookmark(!bookmark)}/> : <BookmarkBorderIcon className="bookmark__icon" onClick = {()=>setBookmark(!bookmark)} />
                    }
                </div>
        </div>
    )
}

export default Zerodha