import React from 'react'
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import './Header.css';
function Header() {
    return (
        <div className="header">
            <h1 className="logo">Logo</h1>
            <div className="header__nav">
                <div className="list">
                    <div className="header__option">Companies</div>
                    <div className="header__option">Holdings</div>
                    <div className="header__option">Order Book</div>
                </div>
                <AccountCircleIcon />
            </div>
        </div>
    )
}

export default Header
